body{
    font-family: "Source Sans Pro", system-ui, -apple-system, "Segoe UI", Roboto, Ubuntu, Cantarell, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    text-rendering: optimizeLegibility;

    background-color: transparent;
    color: #6b717f;
    font-size: 1.125rem;
}
h2, caption, legend, h3, thead th {
    font-family: "Source Serif Pro", "Apple Garamond", "Baskerville", "Times New Roman", "Droid Serif", Times, serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}
h3 {
    margin:1rem 0;
}

header, footer {
    background-color: white;
    box-shadow: 5px 5px 10px 0px rgba(0,0,0,0.5);
    padding: 5px;
}



header {
    border-bottom-right-radius: 7px;
    border-bottom-left-radius: 7px;
    .banner {
        &-container {
            display: flex;
            &__brand {
                margin-right:auto;
                flex-basis: 33.333333%;
            }
            &__title {
                flex-basis: 33.333333%;
                text-align: center;
            }
            &__menu {
                flex-basis: 33.333333%;
                @extend .navigation-menu;
            }
        }
    }
}

.container {
    width: 90vw;
    margin: 0 auto;
    background-color: transparent;

}


#main {
    margin: 20px 0;
    background-color: transparent;
    padding: 3rem 2rem;
    border-radius: 7px;
    min-height:calc(100vh - 190px);
    a{
        color: #036;
    }
    .sl-l-grid {
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        padding: 0;
        list-style: none;
        .sl-l-grid__column {
            flex: 0 0 33.33333%;
            padding-right: 2rem;
            padding-left: 2rem;
        }
    }
    .test-box {
        border: solid 1px rgba(200,200,200,0.75);
        background-color: white;
        padding: 3rem 2rem;
        border-radius: 7px;
    }
    .read-more {
        margin-top:3rem;
        border-top: 1px solid rgba(200,200,200,0.75);
        
    }
}

footer {
    padding: 11px 10px;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    display: flex;
    .footer__copyright {
        flex: 1;
        padding: 0.75rem 1rem;
    }
    nav {
        @extend .navigation-menu;
    }
}

